<script>
export default {
  name: 'tabled',
  data() {
    return {
      info: [


      ]
    }
   },
   methods: {
    updating(id, value) {
        for(let i = 0; i < this.info.length; i++) {
          if(this.info[i].id == id) {
            this.info[i].priceUsd = value;
            this.isTrue = !this.isTrue
          } else {
            
          }
        }
    },    
  },
   filters: {
      price (value) {
        return parseFloat(value).toFixed(3)
      }
  },
   beforeMount() {
      this.$http.get('https://api.coincap.io/v2/assets/')
      .then(response => response.json())
      .then(data => {
        this.info = data.data.slice(0, 15)
      });
      this.ws = new WebSocket('wss://ws.coincap.io/prices?assets=ALL');
      this.ws.onopen = function() {

      };
      this.ws.onmessage = (msg) => {
        let item = JSON.parse(msg.data);
        for(let thing of Object.keys(item)) {
          this.updating(thing, item[thing])
        }
      };
   }
}
</script>



