<template>
  <div id="app">
  	<div class="container">
  		<nav class="container__nav">
	  		<ul class="container__nav__list">
		  		<a href="#"><li class="container__nav__list__item">Coins</li></a>
		  		<a href="#"><li class="container__nav__list__item">Exchanges</li></a>
		  		<a href="#"><li class="container__nav__list__item">Charts</li></a>
		  		<a href="#"><li class="container__nav__list__item">API</li></a>
		  	</ul>
		  	<hr>
		</nav>
		<div class="menu">
			<div class="burger" @click="menuToggle" :class="btn">
				<div class="burger-line line-1"></div>
				<div class="burger-line line-2"></div>
				<div class="burger-line line-3"></div>
			</div>
			<transition name="fade">
				<ul v-show="isOpen" class="container__nav__list__mobile">
				  	<a href="#"><li class="container__nav__list__item__mobile">Coins</li></a>
				  	<a href="#"><li class="container__nav__list__item__mobile">Exchanges</li></a>
				  	<a href="#"><li class="container__nav__list__item__mobile">Charts</li></a>
				  	<a href="#"><li class="container__nav__list__item__mobile">API</li></a>
				</ul>
			</transition>
		</div>

	    <tabled>
	    	
	    </tabled>
  	</div>
  </div>
</template>

<script>

import tabled from './components/tabled.vue'

export default {
  name: 'app',
  data(){
    return {
      isOpen: false,
    }
  },
  components: {
  	tabled
  },
  computed: {
  	btn() {
      return this.isOpen ? 'active' : 'disactive'
    }
	},
  methods: {
	menuToggle() {
	    if(this.isOpen) {
	        this.isOpen = !this.isOpen;
	    } else if (!this.isOpen) {
	        this.isOpen = !this.isOpen;
	    }
	}
  }
}
</script>

